/* search bar */
.search-input {
    padding: 10px 10px;
    height: 52px;
    position: relative;
    padding-bottom:70px;
    width:100%;
    border:none;
    
    
  }
  
  .search-input::before {
    content: url('data:image/png;base64,');
    display: block;
    position: absolute;
    width: 15px;
    z-index: 3;
    height: 15px;
    font-size: 20px;
    top: 20px;
    left: 20px;
    line-height: 32px;
    opacity: 0.6;
  }
  
  .search-input > input {
    width: 100%;
    /* font-size: 18px; */
    border: none;
    line-height: 12px;
    padding: 5px 10px 5px 30px;
    height: 52px;
    position: relative;
    border: 1px solid #137eff4f;
    border-radius: 0px;
  }
  
  .search-input > input:focus {
    outline: none;
  }
  
  .bottom-right {
    position: absolute;
    bottom: 8px;
    right: 16px;
  }
  .bottom-text{
    color:#fff;
    font-size:20px;
    padding:10;
    font-weight: bold;
  }

  .today-card{
    width:200;
    max-height:300;
   background:#ffffff;
  }

  .today-card h5{
    font-size:10px;
    padding:5px 10px 5px 10px;
    text-align:center;
    
    /* max-width: 100%; */
  }
  .today-card h3{
    font-size:12px;
    padding:5px 10px 5px 10px;
    text-align:center;
    background:#ffba47;
    color: #fff;
    font-weight:bold;
    /* max-width: 100%; */
  }


  .mobile-bottom-nav {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    will-change: transform;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    -webkit-box-shadow: 0 -2px 5px -2px #333;
            box-shadow: 0 -2px 5px -2px #333;
    background-color: #fff;
 }

 
  .mobile-bottom-nav__item {
    -ms-flex-positive: 1;
        flex-grow: 1;
    text-align: center;
    font-size: 12px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
 }
  .mobile-bottom-nav__item--active {
    color: #ffba47;
 }
  .mobile-bottom-nav__item-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
 }

